h2 {
    margin: 0px;
}

h4 {
    margin: 0px;
}

li {
    list-style: none;
}

ul {
    margin: 0px;
    padding: 0px;
}

#back-button {
    width: 60px;
    height: 30px;
}

#before-section {
    width: 1280px;
    height: auto;
    margin-bottom: 32px;
}

#vita-compare-section {
    background: rgba(255, 239, 235, 0.2);
    border: 1px solid #000000;
    border-radius: 10px;
    
    margin-top: 32px;
    width: 1280px;
}

#vita-compare-title {
    display: flex;
    gap: 30px;
    margin-top: 32px;
    justify-content: center;
}

#vita-space {
    display: flex;
    padding: 10px;
}

.plus-button {
    text-align: center;
    margin: auto;
}

#functionality-space {
    padding: 10px 0;
}

#functionality-text {
    text-align: center;
}

#vita-functionality-space {
    display: flex;
    padding: 10px 0;
}

.functionality-ul {
    display: flex;
    gap: 10px;
    margin: auto;
    justify-content: center;
    flex: 1;
}

.functionality-li {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#form-space {
    padding: 10px 0;
}

#form-text {
    text-align: center;
}

#vita-form-space {
    display: flex;
    padding: 10px 0;
}

#form-ul {
    display: flex;
    margin: auto;
    justify-content: center;
    flex: 1;
}

.form-li {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.list-text {
    text-align: center;
    font-size: 13px;
    width: 70px;
}

#howtoeat-space {
    padding: 10px 0;
}

#howtoeat-text {
    text-align: center;
}

#vita-howtoeat-space {
    display: flex;
    padding: 10px 0;
}

.vita-howtoeat-text {
    display: flex;
    margin: auto;
    justify-content: center;
    flex: 1;
}

#nutrient-space {
    height: auto;
}

#nutrient-text {
    text-align: center;
}

.nutrient-name-graph {
    display: flex;
    margin: 10px 5px 20px 5px;

    justify-content: center;
    flex: 1;
}

.same-nutrient-name {
    text-align: center;
    padding: 10px 0;
}

.unique-nutrient-name {
    text-align: center;
    padding: 10px 0;
}

.vita-nutrient-graph-space {
    display: flex;
    padding: 10px 0;
}

.nutrient-graph-space {
    width: 100%;
    padding: 10px 0;
    position: relative;
}

.nutrient-range {
    box-sizing: border-box;

    position: absolute;
    width: 350px;
    height: 20px;

    border: 1px solid #000000;
    border-radius: 50px;

    left: 38px;
    top: 0px;
}

.today-range1 {
    box-sizing: border-box;

    position: absolute;
    width: 200px;
    height: 20px;

    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #000000;
    border-radius: 50px 0px 0px 50px;

    left: 38px;
    top: 0px;
}

.today-range2 {
    box-sizing: border-box;

    position: absolute;
    width: 300px;
    height: 20px;

    background-color: rgba(0, 0, 0, 0);
    border: 1px solid #000000;
    border-radius: 50px 0px 0px 50px;

    left: 38px;
    top: 0px;
}

.nutrient-content {
    box-sizing: border-box;

    position: absolute;
    width: 264.63px;
    height: 20px;

    background-color: #FF5C35;
    border: 1px solid #000000;
    border-radius: 50px 0px 0px 50px;

    left: 38px;
    top: 0px;
}

.nutrient-content-text {
    position: absolute;
    left: 100%;
    top: 30px;
}

#add-button {
    text-align: center;
    padding: 10px 0;
}

#caution-space {
    padding: 10px 0;
}

#caution-text {
    text-align: center;
}

#vita-caution-space {
    display: flex;
    padding: 10px 0;
}

.vita-caution-text {
    display: flex;
    margin: auto;
    justify-content: center;
    flex: 1;
}

#buttons-space {
    display: flex;
    padding: 10px 0;
}

#compare-buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;

    margin: auto;
    align-items: center;
    flex: 1;
}

.button {
    width: 250px;
    height: 52px;

    background: #FFFFFF;
    border: 2px solid #FF5C35;
    border-radius: 10px;
}







.vita-component-space {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    margin: auto;
    width: 214.8px;
    height: 360px;

    background: #FFFFFF;
    border: 1px solid #000000;
}

.vita-component-top {
    position: relative;
    width: auto;
    height: 30px;
}

.x-button-space {
    position: absolute;
    left: 182px;
    top: 0px;
    height: 30px;
}

.x-button {
    height: 30px;
    width: 30px;
    padding: 0px;
}

.wish-button-space {
    position: absolute;
    left: 155px;
    top: 15px;
}

.wish-button {
    height: 30px;
    width: 30px;
    padding: 0px;
}

.vita-component-image {
    margin: auto;
}

.vita-component-image-space {
    display: flex;
    box-sizing: border-box;
    align-items: center;
    margin: auto;

    position: relative;
    width: 200px;
    height: 200px;

    background: #FFFFFF;
    border: 1px solid #000000;
}

.vita-component-down {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: auto 10px auto 10px;
}

.wish {
    display: flex;
    margin-left: auto;
    height: 21px;
    align-items: center;
    gap: 10px;
}